.App {
  font-family: sans-serif;
}
body {
  background-color: #22254b;
  color: white;
  margin: 0;
}
.title {
  text-align: center;
}
.movie {
  margin: 0.8em auto;
  width: 200px;
  height: 100%x;
  background-color: #373b69;
  padding: 1em;
  border-radius: 10px;
  box-shadow: 20px 20px 35px rgba(0, 0, 0, 0.699);
  position: relative;
  overflow: hidden;
}
.movie img {
  max-width: 100%;
  border-radius: 3px;
}
.movie-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.movie-info {
  display: flex;
  padding: 0em;
  margin-bottom: 0;
  padding-bottom: 0.3em;
  align-items: center;
  justify-content: space-between;
}
.movie-overview {
  position: absolute;
  background-color: white;
  max-height: 98%;
  border-radius: 3px;
  bottom: 0;
  left: 0;
  right: 0;
  color: #373b69;
  padding: 0.5em;
  transform: translateY(105%);
  transition: transform.3s ease-in-out;
}
.movie:hover .movie-overview {
  transform: translateY(0%);
}

.tag {
  background-color: #22254b;
  padding: 0.2em 0.5em;

  border-radius: 25%;
}
.green {
  color: rgb(2, 66, 2);
}
.orange {
  color: rgb(214, 141, 6);
}

.red {
  color: rgb(226, 1, 1);
}

.header {
  padding: 1em;
}

.button {
  border-radius: 30px;
  margin-left: 16px;
  color: #fff;
  font-size: 1.2em;
  padding: 0.5em;

  background-color: transparent;
}
.search-bar {
  float: right;
}
.search {
  background-color: transparent;
  border: 2px soldi #ffffff;
  font-size: 1.2em;
  border-radius: 50px;
  color: #fff;

  padding: 0.5em;
}

.home-link {
  cursor: pointer;
  border-radius: 10px;
  margin-left: 16px;
  color: #fff;
  font-size: 1.2em;

  padding: 0.5em;
  box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.3);
  float: left;
  background-color: transparent;
}

@media only screen and (max-width: 600px) {
  .search {
    font-size: 0.7em;
    size: 5em;
    background-color: thistle;
    color: black;
    margin-left: 1em;
  }

  .title {
    font-size: 1em;
    text-align: center;
    margin-top: 7em;
    color: black;

    font-size: 0.7em;
    background-color: thistle;
  }
  .button {
    size: 5em;
    border-radius: 8px;
    background-color: thistle;

    color: black;

    font-size: 0.7em;

    padding: 0.5em;
    box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.3);
  }
  .home-link {
    size: 5em;
    font-size: 0.7em;
    background-color: thistle;
    color: black;
  }
  .header {
    background-color: thistle;
  }
}
